.map-container {
  position: relative;
}

.map {
  margin: 0 auto;
  overflow: scroll;
  overflow: -moz-scrollbars-none;
  -ms-overflow-style: none;
  animation-name: fadeIn;
  background: white;
  box-shadow: 0 0 1px 1px rgba(0,0,0,0.1);
  border-radius: 20px;

  @keyframes fadeIn {
    from {opacity: 0;}
    to {opacity: 1;}
  }

  animation-duration: 1.5s;
  animation-fill-mode: forwards;
  //border: 1px dashed $lightborder;
  height: 60vh;
  transition: transform 700ms ease-in-out;

  &::-webkit-scrollbar {
    display: none;
  }

  cursor: move; /* fallback: no `url()` support or images disabled */
  cursor: url('assets/img/icons/ui/grab.cur'), move; /* fallback: Internet Explorer */
  cursor: grab;

  &.dragscroll.grab {
    cursor: url('assets/img/icons/ui/grab.cur'), move;
    cursor: grab;
  }

  &.dragscroll.grabbing {
    cursor: url('assets/img/icons/ui/grabbing.cur'), move;
    cursor: grabbing;
    transition: transform 700ms ease-in-out;
    box-shadow: none;

    &.dragscroll--cancel {
      overflow: hidden;
    }
  }

}

.map__inner {
  position: relative;
  transition: transform 400ms, width 700ms, height 700ms;
  background-image: url('../img/maps/map_present.png');
  background-repeat: no-repeat;
  background-size: contain;
  width: 924px;
  height: 616px;
  margin: 200px;
  image-rendering: -moz-crisp-edges;         /* Firefox */
  image-rendering:   -o-crisp-edges;         /* Opera */
  image-rendering: -webkit-optimize-contrast;/* Webkit (non-standard naming) */
  image-rendering: crisp-edges;
  -ms-interpolation-mode: nearest-neighbor;  /* IE (non-standard property) */

  &.map__inner--1960s1980s {
    background-image: url('../img/maps/map_1960s-1980s.png');
  }

  &.map__inner--1800s {
    background-image: url('../img/maps/map_1800s.png');
  }

  &.map__inner--pre {
    background-image: url('../img/maps/map_pre1800s.png');
  }

}

.movearrows {
  position: absolute;
  z-index: 99999;
  bottom: 0%;
  margin: 1em;
  background-color: white;
  padding: 1em;
  border-radius: 1em;
  border: 2px dashed lightgray;
  width: 70px;
  @media screen and (min-width: 700px) {
    width: 100px;
  }
}

.movearrows--hidden {
  display: none;
}

.icon {
  position: absolute;
  padding: 2em;
  font-size: .875em;
  line-height: 1.6em;

  a {
    text-decoration: underline;
    word-wrap: break-word;
  }

  p {
    margin: .4em 0;
  }

  h3 {
    margin-top: 0;
  }

  &:hover {
    transform: scale(1.05);
    cursor: pointer;
    z-index: 9999;
  }

  .read-more {
    display: flex;
    justify-content: flex-start;
    padding: .5em 0;
    color: black;
    margin-top: 1em;

    p {
      margin: 0;
    }
  }

  .read-more__btn {
    line-height: 0;
    border-radius: 10px;
    padding: 1em 1em 1.25em 1em;
    border: 1px solid;
    text-decoration: none;
    background-color: #eee;
  }

}

.icon-info {
  position: absolute;
  padding: 2em;
  height: auto;
  max-width: 33.5em;
  width: 90%;
  top: 50%;
  left: 50%;
  visibility: hidden;
  transform: translate(-50%, -50%);
  border-radius: 10px;

  p:last-child {
    margin-bottom: 0;
  }

  a {
    text-decoration: underline;
  }
}

.icon-info__title {
  margin-top: 0;
}

.icon-info__close {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  top: 0;
  right: 0;
  padding: .3em;
  background-color: white;
  margin: 1em;

  border-radius: 50%;

  img {
    max-width: 100%;
  }
}

